___CSS_LOADER_EXPORT___.push([module.id, `.mountains {
    --init-brightness: .8;
    --init-saturation: 1.2;
    
    position: absolute;

    width: 100%;
    height: 100%;
    overflow: hidden; 

    background: 
        linear-gradient(
            var(--clr-sky-1) 0%,
            var(--clr-sky-2) 25%, 
            var(--clr-sky-3) 50%, 
            var(--clr-sky-4) 75%
        );
    background-size: auto 200%;
    background-clip: content-box;
    animation: rise-sky var(--rise-time) ease-out 1 forwards;
}

@keyframes rise-sky {
    to {background-position: center 100%;}
}

.mountains img {
    width: 100%;
    object-fit: cover;
    height: 100%;
    position: absolute;
    inset: 0;
    margin: auto;
    z-index: 3;
    pointer-events: none;

    &:nth-of-type(1) {
        --move-speed: 6;
    }
    &:nth-of-type(2) {
        --move-speed: 5;
    }

    animation-name: rise-mountain, parallax ;
    animation-duration: var(--rise-time), auto;
    animation-timing-function: ease-out, linear;
    animation-timeline: auto, scroll(root);
    animation-range: auto, exit;
}

@keyframes rise-mountain {
    from { filter: 
        brightness(var(--init-brightness)) 
        saturate(var(--init-saturation));
    }
}

@keyframes parallax {
    to { 
        scale: 1 calc(1 - var(--move-speed, 0) * 0.01);
        translate: 0 calc(var(--move-speed, 0) * 1vh); 
    }
}`, "",{"version":3,"sources":["webpack://./components/Mountains.css"],"names":[],"mappings":"AAAA;IACI,qBAAqB;IACrB,sBAAsB;;IAEtB,kBAAkB;;IAElB,WAAW;IACX,YAAY;IACZ,gBAAgB;;IAEhB;;;;;;SAMK;IACL,0BAA0B;IAC1B,4BAA4B;IAC5B,wDAAwD;AAC5D;;AAEA;IACI,IAAI,gCAAgC,CAAC;AACzC;;AAEA;IACI,WAAW;IACX,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,QAAQ;IACR,YAAY;IACZ,UAAU;IACV,oBAAoB;;IAEpB;QACI,eAAe;IACnB;IACA;QACI,eAAe;IACnB;;IAEA,wCAAwC;IACxC,0CAA0C;IAC1C,2CAA2C;IAC3C,sCAAsC;IACtC,2BAA2B;AAC/B;;AAEA;IACI,OAAO;;wCAE6B;IACpC;AACJ;;AAEA;IACI;QACI,8CAA8C;QAC9C,6CAA6C;IACjD;AACJ","sourcesContent":[".mountains {\n    --init-brightness: .8;\n    --init-saturation: 1.2;\n    \n    position: absolute;\n\n    width: 100%;\n    height: 100%;\n    overflow: hidden; \n\n    background: \n        linear-gradient(\n            var(--clr-sky-1) 0%,\n            var(--clr-sky-2) 25%, \n            var(--clr-sky-3) 50%, \n            var(--clr-sky-4) 75%\n        );\n    background-size: auto 200%;\n    background-clip: content-box;\n    animation: rise-sky var(--rise-time) ease-out 1 forwards;\n}\n\n@keyframes rise-sky {\n    to {background-position: center 100%;}\n}\n\n.mountains img {\n    width: 100%;\n    object-fit: cover;\n    height: 100%;\n    position: absolute;\n    inset: 0;\n    margin: auto;\n    z-index: 3;\n    pointer-events: none;\n\n    &:nth-of-type(1) {\n        --move-speed: 6;\n    }\n    &:nth-of-type(2) {\n        --move-speed: 5;\n    }\n\n    animation-name: rise-mountain, parallax ;\n    animation-duration: var(--rise-time), auto;\n    animation-timing-function: ease-out, linear;\n    animation-timeline: auto, scroll(root);\n    animation-range: auto, exit;\n}\n\n@keyframes rise-mountain {\n    from { filter: \n        brightness(var(--init-brightness)) \n        saturate(var(--init-saturation));\n    }\n}\n\n@keyframes parallax {\n    to { \n        scale: 1 calc(1 - var(--move-speed, 0) * 0.01);\n        translate: 0 calc(var(--move-speed, 0) * 1vh); \n    }\n}"],"sourceRoot":