___CSS_LOADER_EXPORT___.push([module.id, `.link-tree {
    --width: 15rem;
    --indicator-gap: .5rem;
    --indicator-width: 2rem;
    --link-height: 3.5rem;
    --font-size: 1.2rem;

    @media (width < 32rem) {
        --width: 10rem;
        --font-size: 1rem;
    }

    position: absolute;
    inset: 0;

    width: var(--width);
    height: 100%;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    translate: calc(-1 * var(--width));
    transition: translate .2s ease-out;
    
    z-index: 20;

    opacity: 1;
    pointer-events: all;

    &.hidden {
        pointer-events: none;
        & .trigger {
            opacity: 0;
        }
    }

    .trigger {
        --clr-trigger: #52a5;
        position: absolute;
        left: 100%;
        padding-left: calc(var(--indicator-gap));
    
        width: 2.5rem;
        height: 100%;

        opacity: 1;
        transition: translate .1s, opacity .4s ease-in;

        display: flex;
        align-items: center;

        .indicator {    
            width: .25rem;
            height: 14rem;

            background-color: var(--clr-trigger);
            backdrop-filter: saturate(1.2) blur(.5rem);
            border-radius: .3rem;
        }

        .icon {
            position: absolute;
            left: 1rem;
            color: transparent;
            fill: var(--clr-trigger);
            backdrop-filter: saturate(1.2) blur(.5rem);
        }

        .hint {
            position: absolute;
            left: 1rem;
            width: 24px;
            text-align: center;
            translate: 0 1.25rem;
            font-size: .75rem;
            color: var(--clr-trigger);
            opacity: .75;
        }
    }
    
    .panel {
        position: absolute;
        inset: 0;
        background-color: var(--clr-neutral-light-alpha);
    }
    
    &.hoverEffective:hover, 
    &.expanded {
        translate: 0;

        .trigger {
            transition: translate .1s;
            translate: 
                calc(
                    (var(--indicator-width) + var(--indicator-gap)) 
                    * -1
                );
            opacity: 0;
        }

        .panel {
            border-right: 1px solid var(--clr-neutral-light-alpha);
            backdrop-filter: blur(.75rem);
            z-index: 9;
        }
    }
}

.category {
    width: var(--width);
    height: var(--link-height);
    padding: 0 1rem;
    
    transition: .25s;
    z-index: 10;
    
    display: flex;
    align-items: center;
    
    &::before {
        content: '';
        position: absolute;
        background: 
            linear-gradient(
                to right, 
                var(--clr-secondary), 
                var(--clr-primary)
            );
        filter: saturate(1.2);
        inset: 0;
        opacity: 0;
        transition: .25s;
        width: var(--width);
        height: var(--link-height);
        border-radius: 0 .5rem .5rem 0;
    }

    &.hoverEffective:hover,
    &:has(+ .links.hoverEffective:hover),
    &.selected {
        translate: 1rem;

        &::before {
            width: calc(var(--width) + 1rem);
            translate: -1rem;
            opacity: .2;
        } 
    }    
    
    .icon {
        position: relative;
        size: var(--font-size);
        left: 1rem;
        color: var(--clr-neutral);
    }

    span {
        position: relative;
        left: 1.75rem;
        font-size: var(--font-size);
        font-weight: bold;
        color: var(--clr-neutral);
    } 
}

.links {
    position: absolute;
    inset: 0;

    width: var(--width);
    height: 100vh;
    padding: 1rem 0;
    padding-top: var(--padding, 1rem);
    
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    
    translate: calc(var(--width) * -1);
    transition: translate .2s .1s ease-out;
    
    z-index: 8;
    
    .category.hoverEffective:hover + &,
    .category.selected + &,
    &.hoverEffective:hover {
        translate: var(--width);
    }

    .panel {
        width: calc(var(--width) * 2);
        height: inherit;
        position: absolute;
        left: -100%;
        background-color: var(--clr-neutral-light-alpha);
        border-right: 1px solid var(--clr-neutral-light-alpha);
        backdrop-filter: blur(.25rem);
        z-index: 9;
    }

    .link {
        --link-margin: 0rem;

        text-decoration: none;
        width: calc(var(--width) - var(--link-margin));
        height: var(--link-height);
        margin: 0 var(--link-margin);
        padding-left: calc(2.5rem - var(--link-margin));

        color: var(--clr-neutral);
        font-size: var(--font-size);
        font-weight: bold;
        background: transparent;
        transition: .2s;
        z-index: 10;
    
        display: flex;
        align-items: center;
        
        &.hoverEffective:hover {
            text-decoration: none;
            color: var(--clr-secondary);
            background: linear-gradient(
                to right,
                transparent,
                var(--clr-neutral-light-alpha)
            );
        }

        &.disabled {
            color: var(--clr-disabled);
            cursor: not-allowed;
        }
    }
}

.link-tree .hint {
    position: absolute;
    right: 2rem;
    text-align: center;
    font-size: .75rem;
    color: var(--clr-neutral);
    opacity: .75;
    font-weight: normal;
    transition: .25s;

    &.hidden {
        opacity: 0;
    }

    @media (width < 32rem) {
        opacity: 0;
    }
}`, "",{"version":3,"sources":["webpack://./components/Links.css"],"names":[],"mappings":"AAAA;IACI,cAAc;IACd,sBAAsB;IACtB,uBAAuB;IACvB,qBAAqB;IACrB,mBAAmB;;IAEnB;QACI,cAAc;QACd,iBAAiB;IACrB;;IAEA,kBAAkB;IAClB,QAAQ;;IAER,mBAAmB;IACnB,YAAY;;IAEZ,aAAa;IACb,sBAAsB;IACtB,uBAAuB;;IAEvB,kCAAkC;IAClC,kCAAkC;;IAElC,WAAW;;IAEX,UAAU;IACV,mBAAmB;;IAEnB;QACI,oBAAoB;QACpB;YACI,UAAU;QACd;IACJ;;IAEA;QACI,oBAAoB;QACpB,kBAAkB;QAClB,UAAU;QACV,wCAAwC;;QAExC,aAAa;QACb,YAAY;;QAEZ,UAAU;QACV,8CAA8C;;QAE9C,aAAa;QACb,mBAAmB;;QAEnB;YACI,aAAa;YACb,aAAa;;YAEb,oCAAoC;YACpC,0CAA0C;YAC1C,oBAAoB;QACxB;;QAEA;YACI,kBAAkB;YAClB,UAAU;YACV,kBAAkB;YAClB,wBAAwB;YACxB,0CAA0C;QAC9C;;QAEA;YACI,kBAAkB;YAClB,UAAU;YACV,WAAW;YACX,kBAAkB;YAClB,oBAAoB;YACpB,iBAAiB;YACjB,yBAAyB;YACzB,YAAY;QAChB;IACJ;;IAEA;QACI,kBAAkB;QAClB,QAAQ;QACR,gDAAgD;IACpD;;IAEA;;QAEI,YAAY;;QAEZ;YACI,yBAAyB;YACzB;;;;iBAIK;YACL,UAAU;QACd;;QAEA;YACI,sDAAsD;YACtD,6BAA6B;YAC7B,UAAU;QACd;IACJ;AACJ;;AAEA;IACI,mBAAmB;IACnB,0BAA0B;IAC1B,eAAe;;IAEf,gBAAgB;IAChB,WAAW;;IAEX,aAAa;IACb,mBAAmB;;IAEnB;QACI,WAAW;QACX,kBAAkB;QAClB;;;;;aAKK;QACL,qBAAqB;QACrB,QAAQ;QACR,UAAU;QACV,gBAAgB;QAChB,mBAAmB;QACnB,0BAA0B;QAC1B,8BAA8B;IAClC;;IAEA;;;QAGI,eAAe;;QAEf;YACI,gCAAgC;YAChC,gBAAgB;YAChB,WAAW;QACf;IACJ;;IAEA;QACI,kBAAkB;QAClB,sBAAsB;QACtB,UAAU;QACV,yBAAyB;IAC7B;;IAEA;QACI,kBAAkB;QAClB,aAAa;QACb,2BAA2B;QAC3B,iBAAiB;QACjB,yBAAyB;IAC7B;AACJ;;AAEA;IACI,kBAAkB;IAClB,QAAQ;;IAER,mBAAmB;IACnB,aAAa;IACb,eAAe;IACf,iCAAiC;;IAEjC,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,mBAAmB;;IAEnB,kCAAkC;IAClC,sCAAsC;;IAEtC,UAAU;;IAEV;;;QAGI,uBAAuB;IAC3B;;IAEA;QACI,6BAA6B;QAC7B,eAAe;QACf,kBAAkB;QAClB,WAAW;QACX,gDAAgD;QAChD,sDAAsD;QACtD,6BAA6B;QAC7B,UAAU;IACd;;IAEA;QACI,mBAAmB;;QAEnB,qBAAqB;QACrB,8CAA8C;QAC9C,0BAA0B;QAC1B,4BAA4B;QAC5B,+CAA+C;;QAE/C,yBAAyB;QACzB,2BAA2B;QAC3B,iBAAiB;QACjB,uBAAuB;QACvB,eAAe;QACf,WAAW;;QAEX,aAAa;QACb,mBAAmB;;QAEnB;YACI,qBAAqB;YACrB,2BAA2B;YAC3B;;;;aAIC;QACL;;QAEA;YACI,0BAA0B;YAC1B,mBAAmB;QACvB;IACJ;AACJ;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,kBAAkB;IAClB,iBAAiB;IACjB,yBA