___CSS_LOADER_EXPORT___.push([module.id, `.cover {
    --rise-time: 1.5s;
    --rise-show-time: 1.75s;
    height: 100vh;
    height: 100dvh;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    position: sticky;
    top: 120px;

    background-image: 
        linear-gradient(
            to right, 
            var(--clr-primary),
            var(--clr-secondary)
        );

    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    font-size: 7.5rem;
    text-align: center;
    letter-spacing: .25rem;

    animation: 
        rise-show 
        var(--rise-show-time) 
        ease-in 
        1 
        forwards;

    z-index: 1;
}

.search {
    --height: 3rem;
    --expand-duration: .25s;
    position: sticky;
    top: min(300px, 35vh);

    border-radius: calc(var(--height) / 2);
    overflow: hidden;

    animation: 
        rise 
        var(--rise-time) 
        ease-out 
        1 
        forwards;
    
    z-index: 2;
}

@keyframes rise {
    from { translate: 0 25vh; }
}

.search-form {
    display: flex;
    align-items: center;
    background-color: var(--clr-search-button-light);
    transition: calc(var(--expand-duration) * 2);
    cursor: text;
    
    &:focus-within,
    .search:hover & {
        padding-left: calc(var(--height) / 8);
        padding-right: calc(var(--height) / 2);
        opacity: 1;
        transition: var(--expand-duration);
        background-color: var(--clr-neutral-light);
    }
}

.search-input {
    --clr-text: var(--clr-disabled);
    width: 0;
    height: var(--height);
    font-size: 1.2rem;
    background-color: var(--clr-search-button-light);
    color: var(--clr-text);
    outline: none;
    
    transition: calc(var(--expand-duration) * 2);
    
    &:focus,
    .search:hover & {
        background-color: var(--clr-neutral-light);
        color: var(--clr-text);
        width: 15rem;
        transition: var(--expand-duration);
        &::placeholder {
            opacity: 1;
        }
    }
    
    &::placeholder {
        color: var(--clr-text);
        opacity: 0;
    }
}

.search-icon {
    height: var(--height);
    aspect-ratio: 1 / 1;
    border-radius: 50%;
    display: grid;
    place-items: center;
    opacity: .75;

    font-size: 1.2rem;
    
    .icon {
        color: var(--clr-neutral-light);
        transition: var(--expand-duration);
        animation: 
            rise-show
            var(--rise-show-time)
            ease-in 
            1 
            forwards;
    }

    .search:hover &,
    .search:focus-within & {
        opacity: 1;
        transition: var(--expand-duration);
        .icon {
            color: var(--clr-disabled);
            opacity: 1;
        }
    }
}

.search + .hint {
    position: sticky;
    top: min(355px, calc(35vh + 3.5rem));
    
    color: var(--clr-search-button);
    opacity: 0;
    font-size: .75rem;
    text-align: center;
    
    animation: 
        rise-show
        var(--rise-show-time)
        ease-in 
        1 
        forwards;
    
    z-index: 2;
     
    .search:hover + &,
    .search:focus-within + & {
        opacity: 0 !important;
    }
}

@keyframes rise-show {
    0%, 80% {opacity: 0;}
    100% {opacity: 1;}
}`, "",{"version":3,"sources":["webpack://./components/Cover.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB,uBAAuB;IACvB,aAAa;IACb,cAAc;;IAEd,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,UAAU;;IAEV;;;;;SAKK;;IAEL,qBAAqB;IACrB,6BAA6B;IAC7B,oCAAoC;;IAEpC,iBAAiB;IACjB,kBAAkB;IAClB,sBAAsB;;IAEtB;;;;;gBAKY;;IAEZ,UAAU;AACd;;AAEA;IACI,cAAc;IACd,uBAAuB;IACvB,gBAAgB;IAChB,qBAAqB;;IAErB,sCAAsC;IACtC,gBAAgB;;IAEhB;;;;;gBAKY;;IAEZ,UAAU;AACd;;AAEA;IACI,OAAO,iBAAiB,EAAE;AAC9B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,gDAAgD;IAChD,4CAA4C;IAC5C,YAAY;;IAEZ;;QAEI,qCAAqC;QACrC,sCAAsC;QACtC,UAAU;QACV,kCAAkC;QAClC,0CAA0C;IAC9C;AACJ;;AAEA;IACI,+BAA+B;IAC/B,QAAQ;IACR,qBAAqB;IACrB,iBAAiB;IACjB,gDAAgD;IAChD,sBAAsB;IACtB,aAAa;;IAEb,4CAA4C;;IAE5C;;QAEI,0CAA0C;QAC1C,sBAAsB;QACtB,YAAY;QACZ,kCAAkC;QAClC;YACI,UAAU;QACd;IACJ;;IAEA;QACI,sBAAsB;QACtB,UAAU;IACd;AACJ;;AAEA;IACI,qBAAqB;IACrB,mBAAmB;IACnB,kBAAkB;IAClB,aAAa;IACb,mBAAmB;IACnB,YAAY;;IAEZ,iBAAiB;;IAEjB;QACI,+BAA+B;QAC/B,kCAAkC;QAClC;;;;;oBAKY;IAChB;;IAEA;;QAEI,UAAU;QACV,kCAAkC;QAClC;YACI,0BAA0B;YAC1B,UAAU;QACd;IACJ;AACJ;;AAEA;IACI,gBAAgB;IAChB,oCAAoC;;IAEpC,+BAA+B;IAC/B,UAAU;IACV,iBAAiB;IACjB,kBAAkB;;IAElB;;;;;gBAKY;;IAEZ,UAAU;;IAEV;;QAEI,qBAAqB;IACzB;AACJ;;AAEA;IACI,SAAS,UAAU,CAAC;IACpB,MAAM,UAAU,CAAC;AACrB","sourcesContent":[".cover {\n    --rise-time: 1.5s;\n    --rise-show-time: 1.75s;\n    height: 100vh;\n    height: 100dvh;\n\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.title {\n    position: sticky;\n    top: 120px;\n\n    background-image: \n        linear-gradient(\n            to right, \n            var(--clr-primary),\n            var(--clr-secondary)\n        );\n\n    background-clip: text;\n    -webkit-background-clip: text;\n    -webkit-text-fill-color: transparent;\n\n    font-size: 7.5rem;\n    text-align: center;\n    letter-spacing: .25rem;\n\n    animation: \n        rise-show \n        var(--rise-show-time) \n        ease-in \n        1 \n        forwards;\n\n    z-index